<template>
  <section id="sections">
    <Hero />
    <!-- <About /> -->
    <Client />
    <Work />
  </section>
</template>
  
<script>
import { ref, reactive, onMounted } from 'vue';
// import { db,storage } from '@/firebase/init.js';
import axios from 'axios'


import Hero from '@/views/Main/Section//Hero.vue'
// import About from '@/views/Main/Section/About.vue'
import Work from '@/views/Main/Section/Work.vue'
import Client from '@/views/Main/Section/Client.vue'
export default {
  name: 'Index',
  props: {
  },
  components: {
    Hero,
    // About,
    Client,
    Work,
  },
  setup() {

    const obj = reactive('');
    onMounted(() => {
      objLayout();
    });

    const objLayout = async() => {
      try{
       
        return;
        const storageRef = storage.ref();

        console.log( storage, storageRef );
        // const entityRef = storage.ref('item/apple.json');
        const objectRef = storageRef.child('works.json')
        let url = await objectRef.getDownloadURL()
        let response = await axios.get(url)
        this.item = response.data
        console.log(response.data)
      }catch (error) {
        console.error("error", error);
      }
    }

    return {
      obj,
      objLayout
    };
  }
}
</script>
  
<style lang="scss" scoped>
#sections {
  overflow: hidden;
}
</style>
  