<template>
    <section class="card">
        <div class="inner">
            <h2>Project Work</h2>
            <CardList :workJsonItem="jsonItem" :itemLength="5" />
            <AllWork />
        </div> 
    </section>
</template>
      
<script>
import { ref, onMounted, reactive } from 'vue';
import { defineProps } from 'vue';
import { storage } from '@/firebase/init.js';
import { ref as storageRef, getDownloadURL } from 'firebase/storage';
import axios from 'axios'

 
import CardList from '@/components/Layouts/List/CardList.vue'
import AllWork from '@/components/Layouts/buttons/AllWork.vue'



export default {
    name: 'Work',
    defineProps: {
        itemLength: {
            type: Number,
            default: 5
        },
        workJsonItem: {
            type: Object,
            required: true
        }
    },
    components: {
        CardList,
        AllWork
    },

    setup() {
        const jsonItem = reactive([]);
        const firebaseData = async () => {
            const storageInstance = storage;
            const jsonFileRef = storageRef(storageInstance, `works.json`);
            try {
                const downloadURL = await getDownloadURL(jsonFileRef);
                const res = await axios.get( downloadURL );
                const workitems = res.data; 
                jsonItem.value = workitems;

                
            } catch (error) {
                console.error("Error fetching data:", error);
            }
        } 
        onMounted(() => {
            firebaseData();
        })
        return {
            jsonItem
        };
    }
}
</script>
      
<style lang="scss" scoped>
@import "@/assets/scss/__variables.scss";

section {
    padding: vw(100px 0);
    align-items: start;

    .inner {
        display: block;

        h2 {
            font-size: vw(60px);
            color: $dpColor;
            font-weight: bold;
            letter-spacing: vw(-1px);
            margin-bottom: vw(30px);
        }
    }


}
</style>
      